import './scss/Intro.scss';
import banner from './imgs/banner.png';
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  icon: {
      width: theme.spacing(6),
      height: theme.spacing(6),
  }
}));


export default function Intro() 
{
  const classes = useStyles();
  return (
    <div className="Intro" id="targetIntro">
        <div className='Banner'></div>
        <div className="introDesc">
          <h2 className="Titre"><AssignmentIndIcon className={classes.icon}/>Salut !</h2>
          <p className="introDesc">Je m'appelle Guy Ilunga Boucher et je suis un programmeur. J'adore la programmation orientée objet, les jeux-vidéos et les mangas !</p>
        </div>
        
    </div>
  );
}
