import './scss/Sidebar.scss';
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import FolderIcon from '@material-ui/icons/Folder';
import TrackChangesIcon from '@material-ui/icons/TrackChanges';
import AlternateEmailIcon from '@material-ui/icons/AlternateEmail';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  icon: {
      width: theme.spacing(5),
      height: theme.spacing(5),
  }
}));

export default function Sidebar() {
  const classes = useStyles();
    return (
      <div className="Sidebar">
          <div className="navTargets">
            <a href="#targetIntro"><AssignmentIndIcon className={classes.icon}/></a>
            <a href="#targetProjets"><FolderIcon className={classes.icon}/></a>
            <a href="#targetResume"><TrackChangesIcon className={classes.icon}/></a>
            <a href="#targetContacts"><AlternateEmailIcon className={classes.icon}/></a>
          </div>
      </div>
    );
  }
  