import './scss/Contacts.scss';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import SportsEsportsIcon from '@material-ui/icons/SportsEsports';
import MailIcon from '@material-ui/icons/Mail';
import PhoneIcon from '@material-ui/icons/Phone';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  icon: {
      width: theme.spacing(5),
      height: theme.spacing(5),
  }
}));


export default function Contacts() {
  const classes = useStyles();
  return (
    <div className="Contacts" id="targetContacts">
      <div className="socialMedia">
        <a href="https://www.linkedin.com/in/guy-ilunga-boucher-1184181bb/" target="myPage"><LinkedInIcon  className={classes.icon}/></a>
        <a href="https://github.com/TackyGuy" target="myPage"><GitHubIcon className={classes.icon}/></a>
        <a href="https://tackyguy.itch.io/" target="myPage"><SportsEsportsIcon className={classes.icon}/></a>
        <a href="mailto:boucherilungaguy@gmail.com" target="myPage"><MailIcon className={classes.icon}/></a>
        <a href="tel:+14383406333" target="myPage"><PhoneIcon className={classes.icon}/>438-340-6333</a>
      </div>
    </div>
  );
}
